}

body {
  margin: 0;
  padding: 0;
}

.App {
  position: relative;
 width: 100%;
 height:400vh; 
 display:-webkit-flex; 
 display:flex;
 -webkit-justify-content:center;
         justify-content:center;
 -webkit-flex-wrap: wrap;
         flex-wrap: wrap;
  
  
}


.fondo-circular {
  position: fixed;
  width: 35vw;
  height: 75vh;
  top:12.5vh;
  right:12.5%;
  padding-left:5vw; 
  background:rgb(255,255,255,0.2);
  border-radius: 50%;
  box-shadow: 3px 3px 140px 50px rgb(255,255,255,0.2);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  border-right:1vh solid rgb(255,255,255,0.1);  
  z-index: 10;
  
  
  }
  
  
  
  
  .fondo-circular:before {
    content: "";
  position: absolute;
  width: 28vw;
  height: 28vw;
  background:rgb(255,255,255,0.1); 
  box-shadow: 3px 3px 140px 50px rgb(255,255,255,0.2);
  border-radius: 50%;
  border-bottom:1vh solid rgb(255,255,255,0.1);   
  }
  
  
  
.contenedor-redes {
  position:fixed;
  width:51vw;
  height:9vh;
  right:1.1%;
  top:1.7vh; 
  z-index:2000;
  }

  
.nombre {
  position: absolute;
  width:12vw;
height: 7.5vh;
display: -webkit-flex;
display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: center;
        align-items: center;
left:0%;
color:white;
font-size:2vw; 
font-family: 'Dancing Script' , cursive;
}

.nombre:before {
content: "";
position: absolute;
width:14vw;
height: 0.3vh ;
background:rgb(240,240,240);
border-radius: 20vw; 
left:100%;

}



.redes {
  position: absolute;
  padding-right:1vw;
  padding-left:1vw;
  right: 0%;
  width: 21vw;
  height: 7.5vh;
  background:black;
  background:white; 
  z-index: 1000000000000;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  border: 0.7vh solid rgb(240,240,240);
  border-radius: 50vw;
  background:linear-gradient(-40deg,rgb(62,80,193,1) , rgb(29,146,226,1));
  box-shadow: 2px 2px 5px 2px rgb(0,0,0,0.2);
  box-shadow: 3px 3px 140px 50px rgb(255,255,255,0.2);
  box-shadow: 3px 3px 8px 4px rgb(255,255,255,0.2);
   background:white; 
  }
 
  .redes:before {
  content: "";
  width: 95%;
  position: absolute;
  height: 80%;
  box-shadow: 0px 0px 0px 0.3vh rgb(49,105,206,1);
  border-radius: 50vw;
  
  }

  
.servicio-como {
  position: relative;
width: -webkit-max-content;
width: max-content;
padding: 1vh 0.5vw 1vh 0.5vw ;
border-radius:1vw;
background:white;
box-shadow: 0px 0px 6px 2px rgb(0,0,0,0.1); 
font-family: Segoe UI Symbol;
font-size: 1.1vw;

}

.redes a {
text-decoration:none;
}

.redes a i {
  position: relative;
font-size: 1.5vw;
display: -webkit-flex;
display: flex;
-webkit-align-items: center;
        align-items: center;
color:white;
background:rgb(50,50,50);
box-shadow: 1px 1px 6px 2px rgb(0,0,0,0.2);
padding: 1vh 0.5vw 1vh 0.5vw ;
border-radius: 1vw;
}




.redes a i p {
  position: relative;

font-family: Segoe UI Symbol;
font-size: 1.1vw;
color:white;
padding: 0vh 0vw 0vh 0.3vw ;
margin:0vh 0vh 0vh 0vh; 
cursor: pointer;


}

.redes a .fa-instagram {
background:linear-gradient(120deg,fuchsia,crimson); 
box-shadow: 1px 1px 5px 2px rgb(255,0,255,0.4);
z-index: 2000;
cursor: pointer;
}









